import React from 'react';
import { Link } from 'react-router-dom';

export default function Navbar(){
    return (
        <nav>
            <Link to ='/recipes'>
                <span>
                Recipes
                </span>
            </Link>
            <Link to ='/recipes/jjj'>
                <span>
                Filter by
                </span>
            </Link>
        </nav>
    );
}